<template>
  <v-container>
    <h3>Edit Employee #{{ employee_id }}</h3>
    <v-form @submit.prevent="updateEmployee" ref="updateEmployeeForm">
      <v-text-field
        label="Employee ID"
        required
        v-model="employee_id"
        :rules="idRules"
        readonly
      ></v-text-field>
      <v-text-field
        label="Name"
        required
        v-model="name"
        :rules="nameRules"
        readonly
      ></v-text-field>
      <v-text-field
        label="Department"
        required
        v-model="dept"
        :rules="deptRules"
      ></v-text-field>
      <v-text-field
        label="Position"
        required
        v-model="position"
        :rules="posRules"
      ></v-text-field>
      <v-btn type="submit" color="blue" class="mx-4 mt-3"
        >Update Employee</v-btn
      >
      <v-btn
        color="red"
        class="mt-3"
        :to="{
          name: 'view-employee',
          params: { employee_id: this.$route.params.id }
        }"
        >Cancel</v-btn
      >
      <p style="color: red;">{{ errorMessage ? errorMessage : "" }}</p>
    </v-form>
  </v-container>
</template>

<script>
import db from "@/components/firebaseinit.js";
export default {
  name: "EditEmployee",
  data() {
    return {
      employee_id: "",
      name: "",
      dept: "",
      position: "",
      errorMessage: "",
      nameRules: [
        value => !!value || "You must provide an employee's name",
        value => {
          if (value.length >= 3) {
            return true;
          } else {
            return "Employee's name should contain at least 3 characters.";
          }
        }
      ],
      deptRules: [value => !!value || "Department is required"],
      posRules: [value => !!value || "Position is required"],
      idRules: [value => !!value || "Employee ID is required"]
    };
  },
  beforeCreate() {
    db.collection("employees")
      .where("employee_id", "==", this.$route.params.id)
      .get()
      .then(querySnapshot =>
        querySnapshot.forEach(doc => {
          console.log(doc.data()),
            (this.employee_id = doc.data().employee_id),
            (this.name = doc.data().name),
            (this.position = doc.data().position),
            (this.dept = doc.data().dept);
        })
      );
  },
  methods: {
    updateEmployee() {
      if (this.$refs.updateEmployeeForm.validate()) {
        db.collection("employees")
          .where("employee_id", "==", this.$route.params.id)
          .get()
          .then(querySnapshot =>
            querySnapshot.forEach(doc => {
              doc.ref.update({
                employee_id: this.employee_id,
                name: this.name,
                dept: this.dept,
                position: this.position
              });
              this.$router.push({
                name: "view-employee",
                params: { employee_id: this.employee_id }
              });
            })
          );
      } else {
        this.errorMessage =
          "You must fill all fields before submitting the form.";
      }
    }
  }
};
</script>

<style scoped></style>
